<template>
	<button type="button" href="#" v-bind:disabled="state === 'disabled' || state === 'pending' || state === 'typing'" @click.prevent="$emit('click', $event)">
		<slot name="typing" v-if="state === 'typing'"></slot>
		<slot name="pending" v-if="state === 'pending'"></slot>
		<slot name="active" v-if="state === 'active'"></slot>
		<slot name="disabled" v-if="state === 'disabled'"></slot>
	</button>
</template>

<script>
export default {
	name: 'StatefulButton',
	props: [
		'state'
	]
}
</script>
