				<div class="display-1 mb-3">
					Have you been enjoying Amino?
				</div>
				<div class="lead mb-4">
					Let us know how we're doing.
				</div>
				<b-button-group>
					<b-button class="mr-3" variant="light" pill to="/rate-us/2" @click="updateRated(true)">
						<svg class="feather mr-2">
							<use xlink:href="../assets/images/feather-sprite.svg#thumbs-up" />
						</svg>
						Yes
					</b-button>
					<b-button variant="light" pill to="/rate-us/3" @click="updateRated(false)">
						<svg class="feather mr-2">
							<use xlink:href="../assets/images/feather-sprite.svg#thumbs-down" />
						</svg>
						No
					</b-button>
