		<b-row>
			<b-col>
				<img width="24" src="../assets/images/icon.svg">
			</b-col>
			<b-col cols="auto" class="align-self-center">
				<b-button variant="link" to="/" @click="updateConverted(false)">
					<svg class="feather lg cursor-pointer">
						<use xlink:href="../assets/images/feather-sprite.svg#x" />
					</svg>
				</b-button>
			</b-col>
		</b-row>
        <div class="d-flex flex-grow-1 align-items-center justify-content-center text-center">
            <div>
				<div class="display-1 mb-4">
					Great! Would you help us spread the word?
				</div>
				<div>
					<b-button variant="light" pill href="https://chrome.google.com/webstore/detail/pbcpfbcibpcbfbmddogfhcijfpboeaaf/reviews" target="_blank" @click="updateConverted(true)">
