</template>

<script>
	export default {
		name: 'RateUs',
		computed: {
			slide() {
				return this.$route.params.slide || '1';
			}
		},
		methods: {
			updateRated (positive) {
				this.user.rated.positive = positive;
				this.user.rated.updatedAt = new Date();
				this.user.save();
			},
			updateConverted (converted) {
				this.user.rated.converted = converted;
				this.user.rated.updatedAt = new Date();
				this.user.save();
			}
		},
		mounted () {
				if (!this.user.rated) {
					this.user.rated = {
						updatedAt: new Date(),
						converted: false
					};
					this.user.save();
				}
		}
	}
</script>

