<script>
	import CodeEditor from '@/components/CodeEditor';
	import StatefulButton from '@/components/StatefulButton';
	import UsedStorage from '@/components/amino-vue-components/UsedStorage';
	export default {
		components: {
			CodeEditor,
			StatefulButton,
			UsedStorage
		},
		data() {
			return {
				value: 33.333333333,
				activeAd: false,
				hideActiveAd: false,
				hideAdHours: 4,
				hideEditor: true,
				modes: {
					'THIS_PAGE': 0,
					'THIS_SITE': 1
				},
				mode: 0,
				unsavedChanges: false,
				pendingChanges: false,
				saveButtonState: 'disabled',
				tabUrl: false,
				options: false,
				style: false,
				unreadNotifications: 0,
				notifications: [],
				commandsPort: this.$chrome.runtime.connect({
					name: "commands"
				}),
				loading: true
			}
		},
		computed: {
			isAuthenticated() {
				return this.$store.getters["auth/isAuthenticated"];
			},
			isDisabled () {
				return !this.style || !this.style._id || this.options.disabled;
			},
			toggleStyleLabel () {
				if (this.options.disabled) {
					return 'Amino is disabled';
				}
				if (this.style.active) {
					return 'Disable stylesheet';
				}
				return 'Enable stylesheet';
			},
			user() {
				return this.$store.getters["auth/user"] || {};
			},
			Ad () {
				const {
					Ad
				} = this.$FeathersVuex.api;
				return Ad;
			},
			Notifications() {
				const {
					Notifications
				} = this.$FeathersVuex.api;
				return Notifications;
			},
			NotificationAggregate() {
				const {
					NotificationAggregate
				} = this.$FeathersVuex.api;
				return NotificationAggregate;
			},
			Options() {
				const {
					Options
				} = this.$FeathersVuex.api;
				return Options;
			},
			tabUrlKey() {
				return this.formatKey(this.tabUrl);
			},
			tabUrlParts() {
				return this.getUrlParts(this.tabUrl);
			},
			editorType() {
				return this.$route.params.type || (this.$route.name == 'devtools' ? 'devtools' : 'popup');
			}
		},
		methods: {
			beautify () {
				this.$refs.editor.beautify();
			},
			copyCode () {
				this.$refs.editor.copyCode().then(() => {
					this.makeToast('Copied to clipboard.');
				});
			},
			signOut() {
				this.$store.dispatch('auth/logout')
					.catch()
					.then(() => {
						// maybe do some feedback here
						this.$chrome.storage.sync.remove('apiAccessToken');
						this.$router.push('/sign-in');
						this.openApp('/sign-out');
					});
			},
			setMode(key) {
				this.mode = this.modes[key];
			},
			updateCodeState(unsavedChanges) {
				this.unsavedChanges = unsavedChanges;
				if (this.unsavedChanges) {
					this.saveButtonState = 'active';
				} else {
					this.saveButtonState = 'disabled';
				}
			},
			formatKey(url) {
				const el = this.getUrlParts(url);
				let key;
				switch (this.mode) {
					case 1:
						key = el.hostname;
						break;
					case 0:
						key = url;
						break;
					default:
						key = url;
				}
				return key;
			},
			getUrlParts(url) {
				const el = document.createElement('a');
				el.href = url;

				return el;
			},
			saveCode() {
				this.saveButtonState = 'pending';
				this.$refs.editor.save();
			},
			onSave() {
				this.saveButtonState = 'pending';
			},
			saveSuccess(style) {
				this.style = style;
				this.saveButtonState = 'disabled';
				this.unsavedChanges = false;
			},
			saveError(err) {
				this.saveButtonState = 'active';
				if (err.data) {
					if (err.data.reason === 'QUOTA_EXCEEDED') {
						this.$bvModal.msgBoxOk(err.message, {
								// title: 'Whoops...',
								size: 'sm',
								buttonSize: 'sm',
								okVariant: 'primary',
								okTitle: 'Storage is full. Upgrade to Amino Pro for unlimited storage',
								// headerTextVariant: 'danger',
								// bodyTextVariant: 'danger',
								headerClass: 'p-2 border-bottom-0',
								footerClass: 'p-2 border-top-0',
								centered: true
							})
							.then(value => {
								this.boxTwo = value
							});
					}
				}
			},
			onLoad(style) {
				this.style = style;
			},
			onTyping() {
				this.saveButtonState = 'typing';
			},
			onPending () {
				this.pendingChanges = true;
			},
			discardChanges() {
				this.$refs.editor.discardChanges();
				this.unsavedChanges = false;
				this.saveButtonState = 'disabled';
			},
			updateOptions() {
				this.options.patch();
			},
			updateTheme(mode) {
				this.options.theme = mode;
				this.updateOptions();
			},
			toggleActiveState() {
				this.$store.dispatch('styles/patch', [this.style._id, {
					active: this.style.active,
					draft: this.$refs.editor.hasUnsavedChanges() ? this.$refs.editor.styleCopy.val : false
				}]);
				this.$refs.editor.styleCopy.active = this.style.active;
			},
			dismissAd () {
				this.hideActiveAd = true;
				this.setStorageCookie("hide-editor-ad", true, 60*60*this.hideAdHours);
			},
			loadNotifications() {
				this.notifications = [];
				this.unreadNotifications = 0;
				this.NotificationAggregate.find().then(notifications => {
					let i = 0;
					for (let notification of notifications) {
						if (i < 3) {
							this.notifications.push(notification);
							i++;
						}
						if (!notification.readBy.length) {
							this.unreadNotifications++;
						}
					}
				});
			},
			markNotificationsRead() {
				for (let notification of this.notifications) {
					if (!notification.readBy.length) {
						this.unreadNotifications--;
						notification.readBy.push(this.user._id);
						this.$store.dispatch("notifications/update", [notification._id, {
							$push: {
								readBy: this.user._id
							}
						}]);
					}
				}
			},
			popoutEditor() {
				this.commandsPort.postMessage({
					command: 'popout'
				});
				window.close();
			},
			toggleInspectMode() {
				this.$chrome.activeTab.then(tab => {
					this.$chrome.tabs.sendMessage(tab.id, {
						type: 'inspect'
					});
					if (this.editorType === 'popup') {
						setTimeout(window.close, 300);
					}
				});
			},
			async processChromeTab () {
				const activeTab = await this.$chrome.activeTab;
				const canModifyTabContent = await this.$chrome.canModifyTabContent;
				this.hideEditor = !canModifyTabContent;
				this.tabUrl = activeTab.url;
			}
		},
		async mounted() {
			if (!this.user.rated) {
				const created = this.$moment(this.user.createdAt);
				const today = this.$moment();
				if (today.diff(created, 'days') > 7) {
					this.$router.push('/rate-us');
				}
			}
			this.processChromeTab();
			this.loadNotifications();
			this.Options.find({
				query: {
					userId: this.user.id
				}
			}).then(res => {
				if (res.total === 0) {
					this.options = new this.Options();
					this.options.save();
				} else {
					this.options = new this.Options(res.data[0]);
				}
				setTimeout(() => {
					this.loading = false;
				}, 500);
			});
			this.Notifications.on('created', this.loadNotifications);
			this.Notifications.on('updated', this.loadNotifications);
			this.Options.on('patched', options => this.options = new this.Options(options));

			this.$chrome.tabs.onUpdated.addListener((updatedTabId, changeInfo) => {
				if (changeInfo.url) {
					if (this.$chrome.state.lastValidTab && this.$chrome.state.lastValidTab.url !== changeInfo.url && updatedTabId === this.$chrome.state.lastValidTab.id) {
						this.processChromeTab();
					}
				}
			});
			if (this.editorType !== "popup") {
				this.$chrome.tabs.onActivated.addListener(this.processChromeTab);
				this.$chrome.windows.onFocusChanged.addListener(windowId => {
					if (windowId > 0 && windowId !== this.$chrome.state.activeWindow) {
						this.$chrome.state.activeWindow = windowId;
						this.processChromeTab();
					}
				});
			}
			this.hideEditorAd = await this.getStorageCookie('hide-editor-ad');
			if (!this.isPro && !this.hideEditorAd) {
				try {
					this.activeAd = await this.Ad.get('carbon', { query: { placement: 'extension' } });
				} catch (e) { console.log(e); }
			}
		},
		beforeDestroy() {
			this.Notifications.off('created', () => {});
			this.Notifications.off('updated', () => {});
			this.Options.off('patched', () => {});
		}
	}
</script>

